
import React from "react";


export default class Tip extends React.Component {

    constructor(props) {
        /* eslint-disable */
        super(props);

    }

    useTip() {
        this.props.ControleJogo.useTip()
        this.props.update();
    }



    renderTip() {
        if (this.props.ControleJogo.state.Tips[this.props.ControleJogo.state.focus])

            return (
                <div className="p-5 bg-white  text-black1 arround-12 text-center">
                    <h5 className="text-color1">Dica</h5>
                    {this.props.ControleJogo.Construtor.principal[this.props.ControleJogo.state.focus].Dica}
                </div>
            )
        else
            return <i className="material-icons md-light text-white text-black1 md-7em">lock</i>
    }

    render() {

        return (
            <div>
                {/* <h3>Tip</h3> */}
                 {/* // eslint-disable*/}
                <div className="modal fade " id="staticTips" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered ">
                        <div className="modal-content bg-color1 text-white">
                            <div className="modal-header border-bottom-0">
                                <p className="modal-title" id="staticBackdropLabel">{this.props.ControleJogo.state.focus+1}. {this.props.ControleJogo.Construtor.principal[this.props.ControleJogo.state.focus].Contexto}</p>
                                <button type="button" className="btn-close align-self-start p-2" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div className="modal-body justify-content-center">

                                <div className="p-2 d-flex justify-content-center align-self-center">{this.renderTip()}</div>
                            </div>
                            <div className="modal-footer border-top-0 text-center justify-content-center">
                                <button type="button" className="btn btn-primary" onClick={() => this.useTip()}>Desbloquear dica</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}