
import React from "react";




// ViewsComponents
import Teclado from './ViewComponentsJogo/teclado';
import Tabuleiro from './ViewComponentsJogo/tabuleiro';
import Lista from './ViewComponentsJogo/lista';



export default class ViewJogo extends React.Component {

    constructor(props) {

        super(props);
        this.props.Jogo.init(1)
        console.log(this.props.Jogo.init(1));

        this.state = {
            spinner: false,
            vitoria: true
        }

        // Vinculo entre que força atualização entre os filhos, este componente, e o props.Jogo
        this.update = this.update.bind(this)

        //Funções próprias - caso necessario
        this.next = this.next.bind(this)



    }

    componentDidMount() {

    }





    next() {
        this.props.Jogo.next()
        this.update()
    }

    update() {
        this.forceUpdate()
    }



    render() {

        if (this.state.spinner) {
            return (
                <div className="spinner-border text-secondary" role="status">
                    <span className="visually-hidden">Loading...</span>

                </div>


            );
        } else

            if (!this.props.Jogo.state.hitWord.includes(false))
                return (
                    <div className="d-flex w-100 min-vh-100 align-items-center">
                        <div className="d-flex flex-column align-items-center w-100">
                            <div className="flex-fill text-center w-100">
                                <h1>vitoria</h1>
                            </div>
                        </div>
                    </div>

                );
            else
                return (
                    <div className="d-flex w-100 flex-row container-md min-vh-100 bg align-items-jogo  p-0">
                        <div className="flex-fill d-flex flex-column align-items-center w-100 viewjogo bg-color2 arround-12 ">
                            {/* {this.props.Jogo.Construtor.principal.length} */}
                            <Tabuleiro
                                ControleJogo={this.props.Jogo}
                                update={this.update}
                            />

                            <Teclado
                                ControleJogo={this.props.Jogo}
                                update={this.update}
                            />

                        </div>
                        <div className="flex-fill bg-color2 viewjogo arround-tr-12 arround-br-12 none-md lista">
                            <Lista
                                ControleJogo={this.props.Jogo}
                                update={this.update}
                            />

                        </div>
                    </div>
                );

    }
}