import React from "react";
import Modal from "./ViewComponents/ViewComponentsNav/Modal"
import ModalFull from "./ViewComponents/ViewComponentsNav/ModalFull"

import ViewCredito from "./ViewComponents/View_Credito";
import ViewInstrucao from "./ViewComponents/View_Instrucao";

import View from "./View"
import View2 from "./View2"
import View1 from "./View1";

// Componentes de roteamento
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

import logo from "./../Assets/logomini.png"
/**
 * @param {Null} Null Null
 * @description Classe geradora da função app
 * @constructor
 */

export default function Nav(props) {

    function Tela_Home() {

        return (
            <div className="text-center">
                <h4 className="mb-5">Tem certeza que desaja volta a página inicial?<br /> Você perderá seu progresso no jogo.</h4>

                <Link to="/" data-bs-dismiss="modal">
                    <button type="button" className="button-custom inicial bg-white arround-6" data-bs-dismiss="modal"><span className="material-icons-outlined md-44 align-middle">home</span>INICIO</button>
                </Link>

            </div>
        );
    }


    function Tela_Compartilhar() {
        const instrucao = "Instrução 1 ...!"
        const elemento = ViewInstrucao(instrucao)
        const prev = "/"
        const next = "/instrucao-2"
        return (
            <div className="d-flex flex-row">
                <Nav className="flex-pill" />
                <View2 className="flex-pill" conteudo={elemento} prev={prev} next={next} titulo={"Instruções"} />
            </div>
        );
    }

    function Tela_Creditos() {
        const instrucao = "Instrução 1 ...!"
        const elemento = ViewInstrucao(instrucao)
        const prev = "/"
        const next = "/instrucao-2"
        return (
            <div className="d-flex flex-row">
                <Nav className="flex-pill" />
                <View2 className="flex-pill" conteudo={elemento} prev={prev} next={next} titulo={"Instruções"} />
            </div>
        );
    }

    // ROTEAMENTO - colocar controle de estados da aplicação
    return (
        <div>
            <div className="d-flex nav-main bg-white1 align-items-center">
                <img className='logonav mx-3 my-md-3' src={logo} alt="logo EIC" />
                <i className=" flex material-icons md-dark md-36  mx-3 my-md-3"
                    data-bs-toggle="modal"
                    data-bs-target="#modal-home"
                >home</i>
                <i className=" flex material-icons md-dark md-36  mx-3 my-md-3"
                    data-bs-toggle="modal"
                    data-bs-target="#modal-shared"
                >share</i>
                <i className=" flex material-icons md-dark md-36  mx-3 my-md-3"
                    data-bs-toggle="modal"
                    data-bs-target="#modal-groups"
                >groups</i>

            </div>

            <Modal vinculo="modal-home" titulo="Ateção">
                {Tela_Home()}
            </Modal>
            <ModalFull vinculo="modal-shared" >
                <div className=" view">
                    <ViewCredito className="flex-fill" back={null} />
                </div>
            </ModalFull>
            <ModalFull vinculo="modal-groups">
                <h1>compartilhar</h1>

            </ModalFull >
        </div >
    );
}