import React from "react";

// Importação de componentes
import ViewInicial from "./ViewComponents/View_Inicial";
import ViewCredito from "./ViewComponents/View_Credito";
import ViewInstrucao from "./ViewComponents/View_Instrucao";
import ViewModos from "./ViewComponents/View_Modos";
import ViewJogo from "./ViewJogo";
import View from "./View"
import View2 from "./View2"
import View1 from "./View1";
import Nav from "./Nav";


// Componentes de roteamento
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

// Componentes Front-end
import '../Style/Diretrizes.scss';
import '../Style/Componentes.scss';

// Controladores
import ControleJogo from './Controllers/ControleJogo'



/**
 * @param {Null} Null Null
 * @description Classe geradora da função app
 * @constructor
 */

const jogo = new ControleJogo()
console.log(jogo);

export default function App() {
  // ROTEAMENTO - colocar controle de estados da aplicação
  return (
    <Router basename="/app/cruzadinhas/">
      {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
      <Switch>
        <Route exact path="/">
          <Tela_Inicial />
        </Route>
        <Route exact path="/creditos">
          <Tela_Creditos />
        </Route>
        <Route exact path="/compartilhar">
          <Tela_Compartilhar />
        </Route>
        <Route exact path="/instrucao-1">
          <Tela_Instrucao1 />
        </Route>
        <Route exact path="/instrucao-2">
          <Tela_Instrucao2 />
        </Route>
        <Route exact path="/preparado">
          <Tela_Preparado />
        </Route>
        <Route exact path="/modo">
          <Tela_Modos />
        </Route>

        <Route exact path="/jogo">
          <Jogo />
        </Route>

      </Switch>
    </Router>
  );

  function Jogo() {



    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill"/>
        <ViewJogo Jogo={jogo} />
      </div>
    );
  }

  function Tela_Inicial() {
    
    return (
      <div>
        <View conteudo={<ViewInicial />} />
      </div>
    );
  }
  function Tela_Creditos() {
    return (
      <div>
        <View1 conteudo={<ViewCredito back={true} />} />
      </div>
    );
  }

  function Tela_Compartilhar() {
    return (
      <div>
        <View1 conteudo={<ViewCredito />} />
      </div>
    );
  }


  function Tela_Instrucao1() {
    const instrucao = "Instrução 1 ...!"
    const elemento = ViewInstrucao(instrucao)
    const prev = "/"
    const next = "/instrucao-2"
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill"/>
        <View2 className="flex-pill" conteudo={elemento} prev={prev} next={next} titulo={"Instruções"} />
      </div>
    );
  }
  function Tela_Instrucao2() {
    const instrucao = "Instrução 2 ...!"
    const elemento = ViewInstrucao(instrucao)
    const prev = "/instrucao-1"
    const next = "/preparado"
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill"/>
        <View2 conteudo={elemento} prev={prev} next={next} titulo={"Instruções"} />
      </div>
    );

  }

  function Tela_Preparado() {
    const instrucao = "Preparado? Então vamos começar!";
    const acoes = (
      <div>
        <Link to="/modo">
          <button type="button" className="button-custom inicial bg-white arround-6"><span className="material-icons-outlined md-44 align-middle">sports_esports</span>JOGAR</button>
        </Link>
      </div>
    );
    const elemento = ViewModos(instrucao, acoes)
    const prev = "/instrucao-2"
    const next = null
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill"/>
        <View2 conteudo={elemento} prev={prev} next={next} titulo={"Instruções"} />
      </div>
    );

  }
  function Tela_Modos() {
    const instrucao = null
    const acoes = (
      <div className="d-flex h-100 flex-column">
        <div className="flex-fill w-100 text-center  d-flex justify-content-center align-items-end">

          <Link to="/jogo">
            <button type="button" onClick={jogo.init(1)} className="button-custom modo bg-white arround-6"><span className="material-icons-outlined md-44 align-middle float-start">star_outline</span><span className="float-end align-middle">CURIOSIDADES</span></button>
          </Link>
        </div>
        <div className="flex-fill w-100 text-center  d-flex justify-content-center align-items-center">

          <Link to="/jogo">
            <button type="button" onClick={jogo.init(2)} className="button-custom modo bg-white arround-6"><span className="material-icons-outlined md-44  float-start">star_half</span><span className="float-end align-middle">USO DA PLANTA</span></button>
          </Link>
        </div>
        <div className="flex-fill w-100 text-center  d-flex justify-content-center align-items-start">

          <Link to="/jogo">
            <button type="button" onClick={jogo.init(3)} className="button-custom modo bg-white arround-6"><span className="material-icons-outlined md-44  float-start">star</span><span className="float-end align-middle">NOME POPULAR</span></button>
          </Link>
        </div>
      </div>
    );
    const elemento = ViewModos(instrucao, acoes)
    const prev = null
    const next = null
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill"/>
        <View2 conteudo={elemento} prev={prev} next={next} titulo={"Escolha o tema"} />
      </div>
    );
  }
}
